// COMMON STYLES

.title {
  display: block;
  font-size: 1.25rem;
  line-height: 1;
  text-align: center;
}

// PAGE

.page {
  padding: 2rem;
  width: 100%;
  min-height: 100vh;
  background-image: url("../images/blackboard.jpg");
  background-size: cover;
  background-position: center center;
}

// MAIN

.main {
  display: flex;
  justify-content: center;
  @media all and (max-width: 500px) {
    flex-direction: column;
  }
}
